// ** React Import
import React, {useEffect, Fragment, useState} from 'react'
import {useParams} from 'react-router-dom'
// ** Store & Actions
import { useDispatch, useSelector } from 'react-redux'

import '@styles/base/pages/app-ecommerce-details.scss'
import CommonFooter from "../../../components/CommonFooter"
import {getWardrobe, updateWardrobe} from "../store/action"
import {getAllProducts, uploadImages} from "../../product/store/action"
import {uploadImage} from "../../instagram/store/action"
import {Card, CardBody, Col, CustomInput, Row} from "reactstrap"
import WardrobeInformation from "../layout/components/WardrobeInformation"
import WardrobeModelPhoto from "../layout/components/WardrobeModelPhoto"
import WardrobeCollection from "../layout/components/WardrobeCollection"
import Checkbox from "../../../views/forms/form-elements/checkboxes"

const Detail = () => {
  // ** Vars
  const wardrobeId = useParams().id
  // ** Store Vars
  const dispatch = useDispatch()
  const store = useSelector(state => state.wardrobes)
  const storeProduct = useSelector(state => state.products)
  const [isLoading, setLoading] = useState(false)
    useEffect(() => {
        dispatch(getWardrobe(wardrobeId))
        dispatch(
            getAllProducts()
        )
    }, [dispatch])

    async function handleSubmitButton () {
        if (isLoading) {
            return
        }
        setLoading(true)
        const wardrobe = store.wardrobe
        if (wardrobe.image_file) {
            const formData = new FormData()
            formData.append(`image`, wardrobe.image_file)
            const image = await uploadImage(formData)
            if (image?.data?.data) {
                wardrobe.image_url = image?.data?.data?.url
                wardrobe.image_url_thumb = image?.data?.data?.thumb_url
                wardrobe.image_file = null
            }
        }
        if (wardrobe.back_ground_file) {
            const formData = new FormData()
            formData.append(`image`, wardrobe.back_ground_file)
            const image = await uploadImage(formData)
            if (image?.data?.data) {
                wardrobe.back_ground_url = image?.data?.data?.url
                wardrobe.back_ground_url_thumb = image?.data?.data?.thumb_url
                wardrobe.back_ground_file = null
            }
        }

        if (wardrobe?.wardrobe_images?.length > 0) {
            const formData = new FormData()
            let i = 0
            let isUpload = false
            const wardrobe_images = []
            wardrobe?.wardrobe_images.forEach(wardrobe_image => {
                if (typeof wardrobe_image?.id !== 'number') {
                    isUpload = true
                    formData.append(`images`, wardrobe_image.file.data)
                    i++
                } else {
                    wardrobe_images.push(wardrobe_image)
                }
            })
            if (isUpload) {
                const files = await uploadImages(formData)
                wardrobe?.wardrobe_images.forEach(wardrobe_image => {
                    files?.data?.data?.forEach(file => {
                        if (wardrobe_image?.file?.data?.name === file.originalname) {
                            const image_detail = {}
                            image_detail.file_path = file.originalname
                            image_detail.file_name = file.filename
                            image_detail.image_url = file.url
                            image_detail.thumb_url = file.thumb_url
                            image_detail.mimetype = file.mimetype
                            wardrobe_images.push(image_detail)
                        }
                    })
                })
            }
            wardrobe.wardrobe_images = []
            wardrobe_images.forEach(image_detail => {
                wardrobe.wardrobe_images.push(image_detail)
            })
        }

        if (wardrobe.collection_file) {
            const formData = new FormData()
            formData.append(`image`, wardrobe.collection_file)
            const image = await uploadImage(formData)
            if (image?.data?.data) {
                wardrobe.collection_url = image?.data?.data?.url
                wardrobe.collection_url_thumb = image?.data?.data?.thumb_url
                wardrobe.collection_file = null
            }
        }
        const res = await updateWardrobe(wardrobe, wardrobe.id).catch(
            () => {
                setLoading(false)
            }
        )
        setLoading(false)
        if (res?.data?.status) {
            store.wardrobe = {}
            history?.go(-1)
        } else {
            //show error message
        }
    }

  // ** ComponentDidMount : Get product
  return store.wardrobe?.id ? (
    <Fragment>
      <div className='app-ecommerce-details'>
          {
              isLoading &&
              <div className='loading'>
                  <div className='effect-1 effects'/>
                  <div className='effect-2 effects'/>
                  <div className='effect-3 effects'/>
              </div>
          }
          <Row className='my-2 wardrobe-wrapper'>
              <WardrobeInformation wardrobe={store.wardrobe}/>
              <Col className={`wardrobe col-xl-6 col-sm-12`}>
                  <Card className={'model'}>
                      <CardBody>
                          <h2 className={'wardrobe-title'}>
                              Thông tin mẫu
                          </h2>
                          <div className={'wardrobe-body'}>
                              <WardrobeModelPhoto wardrobe={store.wardrobe}/>
                          </div>
                      </CardBody>
                  </Card>
              </Col>
          </Row>
          <WardrobeCollection wardrobe={store.wardrobe} products={storeProduct.allProducts}/>
          <CommonFooter handleSubmitButton={handleSubmitButton} isLoading={isLoading}/>
      </div>
    </Fragment>
    ) : (
      <div></div>
  )
}

export default Detail
